import React, { useState, useEffect } from "react";
//import EmptyIcon from "../Components/EmptyIcon";
import BasicFinancials from "../Finnhub/BasicFinancials";
import Scrollbar from "../Components/Scrollbar";
import { finnhubClient } from "../Essential";
import "./NewsTab.css";

const NewsTab = (props) => {
  const [isNewsAvailable, setIsNewsAvailable] = useState(false);
  const [news, setNews] = useState([]);

  const getDateFormat = (todaysDate) => {
    let x = todaysDate.getDate();
    let y = todaysDate.getMonth() + 1;
    let z = todaysDate.getFullYear();

    x = x < 10 ? "0" + x : x;
    y = y < 10 ? "0" + y : y;

    return `${z}-${y}-${x}`;
  };

  const getNewsFromFinnhub = (symbol, days = 7) => {
    let currentTime = Math.floor(Date.now() / 1000);

    const to = new Date();
    const from = new Date(to - 1000 * 3600 * 24 * days);

    console.log(to);
    console.log(from);
    console.log(getDateFormat(from), getDateFormat(to));

    finnhubClient.companyNews(
      symbol,
      getDateFormat(from),
      getDateFormat(to),
      (error, data, response) => {
        if (error) {
          console.error(error);
          console.log("response", response);
        } else {
          console.log(data);
          setNews(data);

          setIsNewsAvailable(true);
        }
      }
    );
  };

  const howMuchTimePast = (pastTime) => {
    const currentTime = Math.floor(Date.now() / 1000);

    let x = new Date((currentTime - pastTime) * 1000);

    if (x.getTime() > 1000 * 60 * 60 * 24) {
      return `${x.getDate()}d ago`;
    } else if (x.getTime() > 1000 * 60 * 60) {
      return `${x.getHours()}h ago`;
    } else if (x.getTime() > 1000 * 60) {
      return `${x.getMinutes()}m ago`;
    } else {
      return `0m ago`;
    }
  };

  const deliverNews = () => {
    if (isNewsAvailable) {
      console.log(isNewsAvailable);
      console.log(news);
      return news.map((newsItem, i) => {
        return (
          <a
            className="newsItem"
            key={i}
            rel="noopener noreferrer"
            href={newsItem.url}
            target="_blank"
            style={{
              textDecoration: "none",
              color: "inherit",
              display: "grid",
              justifyContent: "space-between",
              gridTemplateColumns: "auto auto",
              fontSize: "0.8rem",
            }}
          >
            <span
              style={{
                color: "rgba(255, 255, 255, 0.5)",
                display: "inline-block",
              }}
            >
              {" "}
              {howMuchTimePast(newsItem.datetime)} |
            </span>{" "}
            <span>{newsItem.headline}</span>
          </a>
        );
      });
    }
  };

  useEffect(() => {
    getNewsFromFinnhub("TSLA", 3);
  }, []);

  return (
    <div style={{ margin: "5pt" }}>
      <div style={{ fontSize: "2rem", borderBottom: " dotted 2px white" }}>
        News
      </div>

      <Scrollbar style={{}} height={"100%"} background="red">
        <div>{deliverNews()}</div>
      </Scrollbar>
    </div>
  );
};

export default NewsTab;
