import React, { useState, useEffect } from "react";

import Region from "../PageElements/Region";
import { SERVER_URL } from "../Essential";
import { SettingsSystemDaydreamTwoTone } from "@material-ui/icons";
import StockCrawler from "react-stock-crawler/dist";
import { API_KEY } from "../Essential";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import CostumTab from "../PageElements/CustomTab";

const MarketPage = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetch(`${SERVER_URL}/data`)
      .then((resp) => resp.json())
      .then((resp) => setData(resp.data));
  }, []);

  const displayData = (data) => {
    return data.map((d, i) => {
      return <p>{d}</p>;
    });
  };

  return (
    <Container disableGutters={true} maxWidth="lg">
      <Grid item lg>
        <StockCrawler
          style={{ maxHeight: "4rem" }}
          stocks={["AAPL", "MSFT", "NFLX", "TSLA", "ILMN", "FB"]}
          apiKey={API_KEY}
        />
        {displayData(data)}
      </Grid>

      <Grid container direction="row">
        <Grid item xs>
          Index chart
        </Grid>
        <Grid item xs>
          News
        </Grid>
      </Grid>
      <Grid container direction="row">
        <Grid item xs>
          Top GAINERS ...
        </Grid>
        <Grid item xs>
          {" "}
          Earnings, ipo
        </Grid>
      </Grid>
    </Container>
  );
};

export default MarketPage;
