const WatchlistGrid = ({ stockSymbols }) => {
  const [dataArray, setDataArray] = useState(null);
  //const useStyles = makeStyles({});

  const mapData = async (symbol, i) => {
    let price;
    let financials;
    let symb;

    const url1 = `https://finnhub.io/api/v1/stock/metric?symbol=${symbol}&metric=price&token=btmdn7v48v6uocf2j9i0`;
    const url2 = `https://finnhub.io/api/v1/quote?symbol=${symbol}&token=btmdn7v48v6uocf2j9i0`;

    const urls = [url1, url2];
    //Financials
    await Promise.all(
      urls.map((url) => {
        return fetch(url).then((resp) => resp.json());
      })
    ).then((results) => {
      financials = results[0].metric;
      symb = results[0].symbol;
      price = results[1];
    });

    return { price: price, financials: financials, symb: symb };
  };

  const retrieveData = async (data) => {
    let array = await stockSymbols.map(mapData);
    Promise.all(array.map((row) => row)).then((finalResult) =>
      setDataArray(finalResult)
    );
  };

  const displayData = (data) => {
    if (data) {
      console.log(data);
      return data.map((row, i) => {
        console.log(row);
        const { financials, price, symb } = row;
      });
    }
  };

  const classes = useStyles();

  const currencyFormatter = Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  });
  const usdPrice = {
    type: "number",
    valueFormatter: ({ value }) => currencyFormatter.format(Number(value)),
    cellClassName: "font-tabular-nums",
  };
  console.log("rand price");
  return (
    <div>
      <button onClick={retrieveData}>retrieveData</button>

      <div style={{ height: "100%", width: 700 }}>
        <DataGrid
          rowHeight={30}
          headerHeight={35}
          scrollbarSize={15}
          hideFooterRowCount={true}
          hideFooterPagination={true}
          hideFooter={true}
          loading={false}
          onRowClick={console.log}
          className={classes.root}
          columns={[
            { field: "id", type: "number" },
            { field: "username", type: "string" },
            { field: "age", resizable: false, type: "number" },
            { field: "price", ...usdPrice },
          ]}
          rows={[{ id: 1, username: "og", age: 12, price: 12345.786 }]}
        />
      </div>
    </div>
  );
};
