import React, { Component } from "react";
import WatchlistIcon from "../Icons/WatchlistIcon";
import marketIcon from "../Icons/worldwide.svg";
import StockIcon from "../Icons/StockIcon";
import TradeIcon from "../Icons/TradeIcon";
import RobotradeIcon from "../Icons/RobotradeIcon";
import IconWithInfoTip from "../Components/IconWithInfoTip.js";
import Center from "../Components/Center";
import PublicIcon from "@material-ui/icons/Public";
import "./Navbar.css";
import NotificationsActiveIcon from "@material-ui/icons/NotificationsActive";
import Avatar from "@material-ui/core/Avatar";

export default class Navbar extends Component {
  constructor() {
    super();

    this.state = {
      selectedMenuItem: sessionStorage.getItem("selectedMenuItem") * 1,
    };
  }

  getHighlightStyle = (id, value, selectedItem) => {
    if (id === this.state.selectedMenuItem) {
      console.log("match");
      if (value === "className") {
        return "selected";
      } else if (value === "fill") {
        return "aqua";
      }
    }
  };

  handleOnclick = (id) => {
    console.log(this.state.selectedMenuItem);
    if (this.state.selectedMenuItem !== id) {
      console.log(id);
      this.setState({ selectedMenuItem: id });
      sessionStorage.setItem("selectedMenuItem", "" + id);
    }
  };

  render() {
    return (
      <div {...this.props}>
        <div
          style={{
            display: "grid",
            borderRight: "20px",
            borderColor: "white",
            background: "rgb(10, 30, 40)",
            height: "100%",
            gridTemplateRows: "auto auto auto",
            width: "4rem",
            zIndex: "99",
          }}
        >
          <div className="section-1">
            <Center
              className={"navCenter " + this.getHighlightStyle(1, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(1)}
                id={1}
                href="/app/"
              >
                <PublicIcon id={1} fill={this.getHighlightStyle(1, "fill")} />
                <p id={1} className="iconText">
                  Market
                </p>
              </a>
            </Center>

            <Center
              className={"navCenter " + this.getHighlightStyle(2, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(2)}
                href="/app/watchlist"
                id={2}
              >
                <WatchlistIcon
                  size="20pt"
                  className="navIcon"
                  fill={this.getHighlightStyle(2, "fill")}
                />
                <p className="iconText">Watchlists</p>
              </a>
            </Center>

            <Center
              className={"navCenter " + this.getHighlightStyle(3, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(3)}
                href="/app/alerts"
                id={3}
              >
                <NotificationsActiveIcon
                  className="navIcon"
                  fill={this.getHighlightStyle(3, "fill")}
                />
                <p className="iconText">Alerts</p>
              </a>
            </Center>

            <Center
              className={"navCenter " + this.getHighlightStyle(4, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(4)}
                href="/app/stocks"
                id={4}
              >
                <StockIcon
                  size="20pt"
                  className="navIcon"
                  fill={this.getHighlightStyle(4, "fill")}
                />
                <p className="iconText">Stocks</p>
              </a>
            </Center>

            <Center
              className={"navCenter " + this.getHighlightStyle(5, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(5)}
                href="/app/trade"
                id={5}
              >
                <TradeIcon
                  size="25"
                  className="navIcon"
                  fill={this.getHighlightStyle(5, "fill")}
                />
                <p className="iconText">Trade</p>
              </a>
            </Center>

            <Center
              className={"navCenter " + this.getHighlightStyle(6, "className")}
            >
              <a
                className="noStyle"
                onClick={() => this.handleOnclick(6)}
                href="/app/robo-trade"
                id={6}
              >
                <RobotradeIcon
                  size="25"
                  className="navIcon"
                  fill={this.getHighlightStyle(6, "fill")}
                />
                <p className="iconText">Robo-trade</p>
              </a>
            </Center>
          </div>

          <div className="empty-section"></div>

          <div className="section-2">
            <Avatar src="" />
          </div>
        </div>
      </div>
    );
  }
}
